@import "./vars";

#Header {
  $drawer-width: 300px;

  .drawer>div {
    width: $drawer-width;

    .drawer-content {
      .activated{
        color: #fff;
        background-color: $secondary;
        svg{
          color: #fff;
        }
      }
      .avatar {
        box-sizing: border-box;
        display: block;
        width: 175px;
        height: 175px;
        margin: 20px auto;
        border-radius: 100%;
        overflow: hidden;
        box-shadow: #777 0 0 7px;
        img {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: auto;
        }
      }
      .name{
        margin: 10px 0 20px 0;
      }
      .logo{
        margin: 25px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
          width: 80%;
          height: auto;
          filter: invert(75%) sepia(5%);
        }
      }
      .developer{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px 0;
      }
    }
  }

  @media only screen and (max-width: 960px) {
    $drawer-width: 200px;
    
    .drawer>div {
      position: relative;
      width: 100% !important;
      width: $drawer-width;

      .drawer-content {
        .avatar {
          width: 200px;
          height: 200px;
        }
      }
    }
  }
}