@import "./vars";

#Publications {
  box-sizing: border-box;
  margin: 10px 0;
  .data{
    padding: 0;
    li{
      padding: 0;
    }
    .data-item{
      box-sizing: border-box;
      width: 100%;
      padding: 10px 20px;
      margin: 5px 0;
      .link-icon{
        display: flex;
        justify-content: flex-end;
        align-items: center;
      }
    }
  }
}